.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: white;
    width: 60%;
    height: 60%;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

  div.product-name {
    font-size: x-large
  }

  div.product-info {
    margin-top: 4px;
    font-size: large;
    padding-left: 5px
  }

  div.product-image {
    padding: 10px
  }

  table, td, th {
    border: 1px solid black;
    border-collapse: collapse;
    padding: 5px
  }
  
  html {
    font-family: Arial, Helvetica, sans-serif;
  }

  select#set-selector {
    margin-bottom: 8px;
  }